
@charset "UTF-8";
/*
  * @Module: 变量
 * @Author: 小缘
 * @Date: 2017-08-16 23:14:53
 */
#app {
  width: 100%;
  padding-bottom: 20px;
}
#app header {
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background: #4399ff;
    color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    z-index: 99;
}
#app header .logo {
      font-family: 'circle';
}
#app header a {
      color: #fff;
}
@media screen and (min-width: 600px) {
#app {
    padding-top: 60px;
}
#app header {
      height: 60px;
      line-height: 60px;
      padding: 0 50px;
}
#app header .logo a {
        font-size: 52px;
        line-height: 55px;
}
#app header .pc-nav {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        display: block;
        text-align: right;
}
#app header .pc-nav a {
          font-size: 16px;
          display: inline-block;
          padding: 0 20px;
          position: relative;
          opacity: 0.9;
          -webkit-transition: opacity 0.1s;
          transition: opacity 0.1s;
}
#app header .pc-nav a::after {
            content: '';
            width: 100%;
            -webkit-transition: all 0.3s;
            transition: all 0.3s;
            position: absolute;
            left: 0;
            bottom: 0;
            height: 2px;
            background: #fff;
            display: block;
            -webkit-transform: scaleX(0);
                    transform: scaleX(0);
}
#app header .pc-nav a:hover, #app header .pc-nav a.router-link-active {
            opacity: 1;
}
#app header .pc-nav a:hover::after, #app header .pc-nav a.router-link-active::after {
              -webkit-transform: scaleX(1);
                      transform: scaleX(1);
}
#app header .m-nav,
      #app header .sidebar-button {
        display: none;
}
#app header .toggle-menut {
        display: none;
}
}
@media screen and (max-width: 600px) {
#app {
    padding-top: 50px;
}
#app header {
      height: 50px;
      line-height: 50px;
      padding: 0 20px;
}
#app header .pc-nav {
        display: none;
}
#app header .m-nav {
        position: fixed;
        right: 30px;
        top: 55px;
        display: inline-block;
        background: #fff;
        border-radius: 2px;
        -webkit-box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
                box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
}
#app header .m-nav a {
          padding: 15px 30px;
          display: block;
          color: #4399ff;
          line-height: 1;
          font-size: 15px;
}
#app header .m-nav a:active, #app header .m-nav a.router-link-active {
            background: #eee;
}
#app header .sidebar-button {
        margin-right: 25px;
}
#app header .sidebar-button,
      #app header .toggle-menut {
        display: block;
        font-size: 28px;
}
#app header .logo {
        padding-left: 0;
        font-size: 50px;
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        text-align: left;
        line-height: 1;
        position: relative;
        top: -6px;
}
}
.scale-enter-active, .scale-leave-active {
  -webkit-transition: -webkit-transform 0.25s;
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
  -webkit-transform-origin: 100% 0 0;
          transform-origin: 100% 0 0;
}
.scale-enter, .scale-leave-to {
  -webkit-transform: scale(0);
          transform: scale(0);
}



